import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { ModalController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
/**
 * Generated class for the ActividadPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-actividad',
  templateUrl: 'actividad.html',
})
export class ActividadPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, private alertCtrl: AlertController,
  public modalCtrl : ModalController, public events: Events) {

   this.actividadData = {};
   this.ejercicioCompletado = false;


  }


  ionViewWillEnter(){
        //agregar alert de no se cargo ok
    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();
    console.log(this.navParams.get('idActividad'));
    return this.apiProvider.getActividadData(this.navParams.get('idActividad'))
    .then(data => {
    console.log(data);
    this.actividadData = data[0]; 
    loading.dismissAll();
    });

    }


  ionViewDidLoad() {

    }

  actualizarRutina(){
    
    this.events.publish('updateRutin');

    }


	presentAlert(titulo,mensaje) {
	  let alert = this.alertCtrl.create({
	    title: titulo,
	    subTitle: mensaje,
	    buttons: ['Cerrar']
	  });
	  alert.present();
	}


    openModal(){
    var modalPage = this.modalCtrl.create('LoginPage');
    modalPage.present();
    }



   completarEjercicioApi() {

    let loading = this.loadingController.create({content : "cargando..."});
	loading.present();

    this.apiProvider.completarEjercicio(this.navParams.get('idActividad'),this.navParams.get('idR'),1)
    .then(data => {
      console.log(data);
      loading.dismissAll();  
      if(data.data.affectedRows > 0){	
      	this.ejercicioCompletado = true;
      	this.presentAlert('Enhorabuena!','Has completado el ejercicio');
         this.events.publish('updateRutin');
      }
          
    });
  }


  completarEjercicio() {
  let alert = this.alertCtrl.create({
    title: 'Completar',
    message: 'Deseas marcar como completado este ejercicio?',
    buttons: [
      {
        text: 'Cancelar',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      },
      {
        text: 'Completar',
        handler: () => {
          console.log('COMPLETADO');
          this.completarEjercicioApi();
        }
      }
    ]
  });


  this.apiProvider.verificarLogin()
    .then(data => {    
      console.log(data);
      data ? alert.present() : this.openModal();
     });

  
}








}



